	import {publicing} from '../../../api/api.js'
	import {addOrder} from '../../../api/request.js'
export default {
	data() {
		let that = this;
		return {
			model: {
				name: '',
				phone: '',
				deliveryUnit: '',
				code: ''
			},
			changeModel:{},
			rules: {
				name: [
					{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					},
					{
						min: 3,
						max: 5,
						message: '姓名长度在3到5个字符',
						trigger: ['change', 'blur']
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.chinese(value);
						},
						message: '姓名必须为中文',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change', 'blur']
					}
				],
				code: [
					{
						required: true,
						message: '请输入取件码',
						trigger: 'blur'
					},
					{
						min: 4,
						max: 6,
						message: '取件码在4到6个数字',
						trigger: ['change', 'blur']
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.number(value);
						},
						message: '取件码必须为数字',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change', 'blur']
					}
				],
				phone: [
					{
						required: true,
						message: '请输入手机号',
						trigger: ['change', 'blur']
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.mobile(value);
						},
						message: '手机号码不正确',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change', 'blur']
					}
				]
			},
			deliveryUnitList: [
				{
					value: '中通',
					label: '中通'
				},
				{
					value: '申通',
					label: '申通'
				},
				{
					value: '圆通',
					label: '圆通'
				},
				{
					value: '百世',
					label: '百世'
				}
			],

			// 底部选项栏
			selectShow: false,
			border: true,
			labelPosition: 'left',
			check: false,
			selectStatus: 'close',

			radioCheckWidth: 'auto',
			radioCheckWrap: false,

			codeTips: '',
			errorType: ['message']
		};
	},
	onLoad() {},
	computed: {
		borderCurrent() {
			return this.border ? 0 : 1;
		},
		newName(){
			this.model.name=this.$store.state.address.name;
			return this.$store.state.address.name
		},
		newPhone(){
			this.model.phone=this.$store.state.address.phone;
			return this.$store.state.address.phone
		}
	},
	onReady() {
		this.$refs.uForm.setRules(this.rules);
	},
	methods: {
		// 下单  提交表单
		submit() {
			//检查信息ok后，进行调用
			console.log('提交信息');
			let data={
				eInitId:uni.getStorageSync("userInfo").id,
				eAddressId:this.$store.state.address.id,
				eType:this.model.deliveryUnit,
				eTakeCode:this.model.code,
				eMoney:"2元",
			}
			console.log(data)
			publicing(addOrder,data)
			.then((res)=>{
				console.log(res);
				if(res.data==="success"){
					this.$u.toast("成功加入订单!");
					//跳转到订单支付页面
				}
				else if(res.data==="repetition"){
					this.$u.toast("订单重复了哟!");
				}
				else{
					this.$u.toast("服务器访问出错!");
				}
			})
			.catch((err)=>{
				this.$u.toast("服务器访问出错!");
			})
		},
		// 底部状态栏
		selectConfirm(e) {
			this.model.deliveryUnit = '';
			e.map((val, index) => {
				this.model.deliveryUnit += this.model.deliveryUnit == '' ? val.label : '-' + val.label;
			});
		}
	}
};
